import React, { Component } from 'react';
import { NavLink } from "react-router-dom";
import { Carousel, WhiteSpace } from 'antd-mobile';
import { observer } from 'mobx-react';
import './index.scss'

class List extends Component {


    componentDidMount() {
        // if (!WordsList.shequList.length > 0) {
        // }
        // console.log(this.props.goods)
    }

    render() {
        const {
            conentList
        } = this.props;
        return (
            <div style={{paddingTop:40}}>
                {
                    conentList.map((item, i) => {
                        return (
                            <div key={i}>
                                
                                <Carousel className="space-carousel"
                                    autoplay={false}
                                    infinite={false}
                                    frameOverflow="hidden"
                                    cellSpacing={10}
                                    slideWidth={0.8}
                                    dots={false}
                                >
                                    <NavLink
                                        to={'/detail/' + item.id}
                                        style={{
                                            display: 'block',
                                            position: 'relative',
                                            height: '6rem',
                                            width: '100%',
                                            boxShadow: '2px 1px 1px rgba(0, 0, 0, 0.2)',
                                        }}
                                    >
                                    <header style={{color:'#000', margin:0 -5,width:311,transform: 'translate3d(4.5px, 5px, 0px)',boxShadow: 'rgba(3, 0, 0, 0.2) 6px 1px -2px'}} className="head">
                                    <img className="touxiang" style={{ width: '100%', height: '4rem' }} src={item.img} alt="" />
                                    <span>{item.creator}</span>
                                </header>
                                        <img
                                            src={item.img}
                                            alt=""
                                            style={{ width: '100%', height: '3rem' }}
                                            className="list-img"
                                        />
                                        <div className="list-box-contenet" style={{ background: '#fff', height: '6rem', color: '#000'}}>
                                            <section  className="list-hitokoto">{item.hitokoto}</section>
                                            <h2 className="list-from">-《{item.from}》-</h2>
                                            <p className="list-text">#{item.type.text}</p>
                                        </div>
                                    </NavLink>
                                </Carousel>
                            </div>

                        )
                    })
                }

            </div>
        )
    }
}

export default List