import React, { Component } from 'react';
import './index.scss';
import { Head } from '&/components/head';
import List from '&/components/list';
import { axios } from '~'
import { PullToRefresh } from 'antd-mobile';

export class Swop extends Component {
    state = {
        wordList: [],
        refreshing: false
    }

    componentDidMount() {
        axios.get('/react/tuijianbook', {

        }).then(res => {
            const list = res.data.result;
            this.setState({
                wordList: list
            })
        })
    }

    render() {
        return (
            <div>
                <Head title='圈子' show={true}></Head>
                {/* <h2>swop-swop</h2> */}

                <PullToRefresh
                    damping={60}
                    ref={el => this.ptr = el}
                    style={{
                        height: this.state.height,
                        overflow: 'auto',
                    }}
                    indicator={{ deactivate: '上拉可以刷新' }}
                    direction={'down'}
                    refreshing={this.state.refreshing}
                    onRefresh={() => {
                        this.setState({ refreshing: true });
                        console.log("下拉刷新...")
                        setTimeout(() => {
                            axios.get('/react/tuijianbook', {

                            }).then(res => {
                                const list = res.data.result;
                                this.setState({
                                    wordList: list
                                })
                            })
                            this.setState({ refreshing: false });
                        }, 600);
                    }}
                >
                    <List
                        conentList={this.state.wordList}
                    ></List>
                </PullToRefresh>
                    <div className="bottom-txt">我也是有底线的</div>
            </div>
        )
    }
}