import React, { Component } from 'react';
import './index.scss';
import { axios,baseURL,history } from '~';
import { Button, WhiteSpace, List,Modal } from 'antd-mobile';
const Item = List.Item;
const alert = Modal.alert;


const initPic = 'http://47.93.241.71/Ajun/img/login-title.c0354687.jpg'

export class Upload extends Component {
    constructor(){
        super();
        this.state={
            txt: '',
            imgUrl: initPic
        }
    }

    quitLogin = () =>{
        sessionStorage.token ='';
        history.push('/login')
    }

    // 退出登录
    showAlert = () => {
        const alertInstance = alert('', '是否退出登录', [
          { text: '取消', onPress: () => console.log('cancel'), style: 'default' },
          { text: '确定', onPress: () => this.quitLogin() },
        ]);
        setTimeout(() => {
          // 可以调用close方法以在外部close
          console.log('auto close');
          alertInstance.close();
        }, 500000);
      };

    //   获取头像
    componentDidMount(){
        const userInfo = localStorage.getItem('userInfo');
        if(userInfo){
            userInfo = JSON.parse(userInfo);
            if(userInfo.mobile==sessionStorage.mobile){
                this.setState({
                    imgUrl:userInfo.path
                })
            }else{
                this.getPic()
            }
        }else{
            this.getPic();
        }
    }

    getPic = ()=>{
        axios.post('/react/getAvatarPic').then(res=>{
            if(!!res.data.type){
                this.setState({
                    imgUrl: res.data.path.replace('public',baseURL)
                })
            }else{
                this.setState({
                    imgUrl: initPic
                })
            }
        })
    }

    startSend = ()=>{
        this.file.click()
    }

    // 上传头像
    uploadFile = ()=>{
        console.log('start upload')
        const file = this.file.files[0];
        const data = new FormData();
        data.append('avatar',file);
        axios({
            url: '/react/uploadavatar',
            method: 'POST',
            data
        }).then(res=>{
            this.setState({
                imgUrl: res.data.path.replace('public',baseURL)
            })
            localStorage.imgUrl=res.data.path.replace('public',baseURL)
        })
    }

    render() {
        console.log(history)
        const {
            txt
        } = this.props
        return (
            <div>
                <div className="user-info">
                    <div className="avatar">
                        <img onClick={this.startSend} style={{ width: 90, height: 90 }} src={this.state.imgUrl} alt="" />
                        <input type="file" onChange={this.uploadFile} ref={el=>this.file=el} style={{ display: 'none' }} />
                        <p  style={{color: '#1E1E1E',fontWeight:700}}>欢迎回来：{txt}</p>
                    </div>
                </div>
                <List renderHeader={() => '我的'} className="my-list">
                    <Item arrow="horizontal" platform="android" multipleLine onClick={() => { history.push('/ilike')}}>
                        我喜欢的
                    </Item>
                    <Item
                        arrow="horizontal"
                        multipleLine
                        onClick={() => { history.push('/ishoucang') }}
                        platform="android"
                    >
                       我收藏的
                    </Item>
                </List>

                <List renderHeader={() => '操作'} className="my-list">
                    <Item arrow="horizontal" platform="android" multipleLine onClick={() => { }}>
                        清除缓存
                    </Item>
                    <Item
                        arrow="horizontal"
                        multipleLine
                        onClick={this.showAlert}
                        platform="android"
                    >
                       退出登录
                    </Item>
                </List>
            </div>
        )
    }
}