import React, { Component } from 'react';
import './index.scss';
import { Head } from '&/components/head'
import { SearchBar } from 'antd-mobile';
import { axios } from '~';
import List from "&/components/list";

export class Search extends Component {
    state = {
        value: '',
        wordsList: []
    };
    onChange = (value) => {
        this.setState({ value });
    };

    onSubmit = (value) => {
        //   console.log(value)
        const val = value.replace(/\s/g, '')
        if (val) {
            axios.get('/react/getWordList', {
                params: {
                    keyword: val
                }
            }).then(res => {
                // console.log(res.data.result)
                const words = res.data.result
                this.setState({
                    wordsList: words,
                })
            })
        }
    }
    render() {
        console.log(this.state.wordsList.length)
        return (
            <div>
                <Head title='搜索' show={true}></Head>
                <SearchBar value={this.state.value} placeholder="请输入关键字" onChange={this.onChange} onSubmit={value => this.onSubmit(value)} />

                <List
                    conentList={this.state.wordsList}
                ></List>

                <p style={{ display: this.state.wordsList.length > 0 ? 'none' : 'block' }} className="search-content">暂时没有您要的内容哦！</p>


            </div>
        )
    }
}