import {observable,action,computed} from 'mobx';
import {axios} from '~';
class WordsList{
    @observable  booksList = [];
    @observable  wordsList = []
           
    @action getBookList = (id)=>{
        axios.post('/react/getWords',{
            id
        }).then(res=>{
            this.booksList = res.data.result;
        })
    }
    // 首页
   @action getWordList = ()=>{
        axios.post('/react/getWordList',{

        }).then(res=>{
            this.wordsList = res.data.result;
        })
   }
}

export default new WordsList();