import React, { Component } from 'react';
import './index.scss';
import { Head } from '&/components/head';
import {axios} from '~';
import List from '&/components/list'
import { Tabs, WhiteSpace } from 'antd-mobile';

const tabs = [
    { title: '动画' },
    { title: '漫画' },
    { title: '游戏' },
    { title: '小说' }
];

 class Shequ extends Component {
    constructor(){
        super()
        this.state={
            tabs,
            wordList:[]
        }
    }


    componentDidMount(){
        axios.get('/react/getWordList',{}).then(res=>{
            const list = res.data.result;
            this.setState({
                wordList:list
            })
        })
    }
    render() {
        return (
            <div>
                <Head title='社区' show={true} style={{borderBottom:'1px solid #000'}}></Head>
                <Tabs tabs={tabs}
                    initalPage={'t2'}
                >
                {
                    tabs.map((item,i)=>{
                        return(
                            <List
                            key={i}
                            type={item.title}
                            conentList={this.state.wordList.filter(val=>val.type.text==item.title)}
                            >
                            </List>
                        )
                    })
                }
                </Tabs>
            </div>
        )
    }
}

export default Shequ