import React, { Component } from 'react';
import './index.scss';
import { NavBar, Icon } from 'antd-mobile';
import { history } from '~'

export class Head extends Component {

    constructor() {
        super();
        this.state = {
            isFanhui: false,
            isSearch: true
        }
    }

    goSearch = () => {
        history.push('/search')
    }

    gofanhui = () => {
        history.go(-1)
    }

    componentDidMount() {
        const main = location.hash.split('#/')[1];
        if (main == 'search'||main=='login') {
            this.setState({
                isFanhui: true,
                isSearch: false
            })
        }
        if(main=='ilike'||main=='ishoucang'){
            this.setState({
                isFanhui: true,
            })
        }
        // if(main=='login'){
        //     this.setState({
        //         isFanhui: true,
        //         isSearch: false

        //     })
        // }
    }

    render() {
        const {
            title,
            show
        } = this.props;
        const {
            isFanhui,
            isSearch
        } = this.state;
        return (
            <div>
                <NavBar
                    mode="light"
                    icon={isFanhui && <Icon type="left" onClick={this.gofanhui} />}
                    onLeftClick={() => console.log('onLeftClick')}
                    rightContent={[
                        isSearch && <Icon onClick={this.goSearch} key="0" type="search" style={{ marginRight: '16px' }} />,
                        // <Icon key="1" type="ellipsis" />,
                    ]}
                >{title}</NavBar>
            </div>
        )
    }
}