import React, { Component } from 'react';
import './index.scss';
import { NavLink } from "react-router-dom";
import { Head } from '&/components/head';
import { history, axios } from '~';
import { Carousel, WhiteSpace, Toast } from 'antd-mobile';
import { observer } from 'mobx-react';
import WordsList from '&/mobx/wordList';

@observer
class Home extends Component {

    constructor() {
        super();
        this.state = {
            words: []
        }
    }

    componentDidMount() {
        // axios.post('/react/getWordList', {

        // }).then(res => {
        //     // console.log(res.data.result)
        //     const word = res.data.result
        //     this.setState({
        //         words: word
        //     })
        // })
        if(!WordsList.wordsList.lenght>0){
            WordsList.getWordList()
        }
        console.log(WordsList.wordsList.lenght)
    }

    render() {
        return (
            <div>
                <Head title='首页' show={true}></Head>
                <WhiteSpace size="xl" />
                <Carousel className="space-carousel"
                    autoplay={false}
                    infinite={false}
                    frameOverflow="hidden"
                    cellSpacing={10}
                    slideWidth={0.8}
                    dots={false}
                >
                    {WordsList.wordsList.map((item, index) => (
                        <NavLink
                            to={'/detail/' + item.id}
                            key={index}
                            style={{
                                display: 'block',
                                position: 'relative',
                                height: '10rem',
                                width: '100%',
                                boxShadow: '2px 1px 1px rgba(0, 0, 0, 0.2)',
                            }}
                        >
                            <img
                                src={item.img}
                                alt=""
                                style={{ width: '100%', height: '3rem' }}
                            />
                            <div style={{ background: '#fff', height: '7rem', color: '#000' }}>
                                <section className="hitokoto">{item.hitokoto}</section>
                                <h2 className="from">-《{item.from}》-</h2>
                                <p className="text">#{item.type.text}</p>
                                <p className="book-info">
                                    <span>{item.creator}</span>
                                    <span>摘抄于</span>
                                    <span>{item.from}</span>
                                </p>
                            </div>
                        </NavLink>
                    ))}
                </Carousel>
            </div>
        )
    }
}

export default Home