import React, { Component } from 'react';
import './index.scss';
import { Head } from '&/components/head'
import { observer } from 'mobx-react';
import WordsList from '&/mobx/wordList';
import { history } from '~';
import { NavLink } from 'react-router-dom';
import { Icon } from 'antd-mobile';
import { axios } from '~';


export const foots = [
    { path: "", name: "", icon: "icon-xihuan" },
    { path: "", name: "", icon: "icon-xiaoxi" },
    { path: "", name: "", icon: "icon-shuqian" }
]

@observer
class Detail extends Component {

    state = {
        foots,
        likeFalg: true,
        wid: '',
        typeFlag: 0,
        typeShoucang: 0
    };

    // 判断是否喜欢
    myWord = (type) => {
        if (!type) {
            this.setState({
                typeFlag: type
            })
        } else {
            this.setState({
                typeFlag: type
            })
        }
    }

     // 判断是否收藏
     myShoucang = (type) => {
        if (!type) {
            this.setState({
                typeShoucang: type
            })
        } else {
            this.setState({
                typeShoucang: type
            })
        }
    }

    fanhui = () => {
        history.go(-1)
    }

    like = (url) => {
        const id = this.state.wid;
        axios.get(url, {
            params: {
                id,
                word: WordsList.booksList
            }
        })
        .then(res => {
            const type = res.data.type;
                // this.myWord(type)
                console.log(res.data.msg)
            if(url=='/react/getLike'){
                this.myWord(type)
            }else{
                this.myShoucang(type)
            }
        })
    }

    // 点击按钮添加喜欢
    clickType = () => {
        this.like('/react/getLike')
    }

    // 点击按钮添加收藏
    clicShoucang = () => {
        this.like('/react/getShoucang')
    }

    componentDidMount() {
        // 从地址栏获取url参数id
        const id = this.props.match.params.id;
        this.setState({
            wid: id
        })
        if (!WordsList.booksList.length > 0) {
            WordsList.getBookList(id)
        }


        // 进入页面判断是否喜欢
        axios.get('/react/ilike', {
            params: { id }
        }).then(res => {
            const type = res.data.type;
            this.myWord(type)
        })

        // 进入页面判断是否收藏
        axios.post('/react/ishoucang',{
            id
        }).then(res=>{
            const types = res.data.type;
            this.myShoucang(types)
        })
    }

    render() {
        const {
            booksList
        } = WordsList;
        return (
            <div>
                <header className="head">
                    <img className="touxiang" style={{ width: '100%', height: '4rem' }} src={booksList.img} alt="" />
                    <span>{booksList.creator}</span>
                </header>
                <div className="content">
                    <img className="content-img" src={booksList.img} alt="" />
                    <section>
                        <p>{booksList.hitokoto}</p>
                        <h2>-《{booksList.from}》-</h2>
                    </section>
                </div>
                <div className="detail-foot">
                    <Icon onClick={this.fanhui} style={{ margin: '12 0 0 5' }} type="left" />
                    <div>
                        <p>
                            <i style={{ color: (this.state.typeFlag) ? '#000' : 'dimgray' }} onClick={() => this.clickType()} className='iconfont icon-xihuan'></i>
                        </p>
                    </div>
                    <div>
                        <p>
                            <i className='iconfont icon-xiaoxi'></i>
                        </p>
                    </div>
                    <div>
                        <p>
                            <i style={{ color: (this.state.typeShoucang) ? '#000' : 'dimgray' }} onClick={() => this.clicShoucang()} className='iconfont icon-shuqian'></i>
                        </p>
                    </div>
                </div>
            </div>
        )
    }
}

export default Detail;