import React, { Component } from 'react';
import './index.scss'; import { Head } from '&/components/head';
import List from '&/components/list';
import { axios } from '~'
import { NavLink } from "react-router-dom";

class Shoucang extends Component {
    state = {
        iShouCang :[]
    }

    componentDidMount() {
        const shoucang = []
        axios.post('/react/getWordShou').then(res => {
            // console.log(res.data.result)
            for (let i = 0; i < res.data.result.length; i++) {
                shoucang.push(JSON.parse(res.data.result[i].word))
            }
            this.setState({
                iShouCang: shoucang
            })
        })
    }

    render() {
        return (
            <div>
                <Head
                    title="我的收藏"
                ></Head>
                <List
                    conentList={this.state.iShouCang}
                >
                </List>
            </div>
        )
    }
}

export default Shoucang