import React, { Component } from 'react';
import './index.scss';
import { Head } from '&/components/head';
import { Button, WhiteSpace } from 'antd-mobile';
import { history,axios } from '~';
import { Upload } from '&/components/upload'

export class My extends Component {
    constructor() {
        super()
        this.state = {
            userinfoFalg: !!sessionStorage.token,
        }
    }


    goLogin = () => {
        console.log(111)
        history.push('/login')
    }

    componentDidMount() {
        axios.post('react/getMobile').then(res=>{
            if(!!res.data.type){
                this.setState({
                    mobile:res.data.result
                })
            }
        })
    }

    render() {
        const {
            userinfoFalg
        } = this.state;
        return (
            <div>
                <Head title='个人中心' show={true}></Head>
                {
                    !userinfoFalg && <div className="my-box">
                        <h2 className="title">登录后，呈现更多精彩内容</h2>
                        <Button onClick={this.goLogin} className="go-login" type="primary" inline style={{ marginRight: '4px' }}>去登录</Button>
                    </div>
                }
                {
                    userinfoFalg && <Upload txt={this.state.mobile}></Upload>
                }
            </div>
        )
    }
}