import React, { Component } from 'react';
import './index.scss';
import { Head } from '&/components/head';
import { WhiteSpace, WingBlank, List, InputItem, Button } from "antd-mobile";
import {axios,history} from '~';

const mobileReg = /^1(3|5|7|8|9)\d{9}$/;
const codeReg = /^\d{4}$/;

export class Login extends Component {
    constructor() {
        super()
        this.state = {
            count: 60,
            txt: '发送验证码',
            mobileFlag: false,
            loginFlag: false,
            toggle: true
        }
    }
    // 手机号判断
    checkMobile = (mobile) => {
        if (this.state.toggle) {
            this.setState({
                mobileFlag: mobileReg.test(mobile),
                loginFlag: (codeReg.test(this.code.state.value) && mobileReg.test(mobile))
            })
        }
    }
    // 验证码正则
    checkCode = (code) => {
        this.setState({
            loginFlag: (codeReg.test(code) && mobileReg.test(this.mobile.state.value))
        })
    }

    // 确认登录并校验验证码
    todoLogin = () => {
        axios.post('react/checkCode',{
            mobile: this.mobile.state.value,
            code: this.code.state.value
        }).then(res=>{
            if(!!res.data.type){
                sessionStorage.token = res.data.token;
                sessionStorage.mobile = this.mobile.state.value;
                history.push('/main/home');
            }else{
                sessionStorage.token = '';
            }
        })
    }

    sendCode = () => {
        axios.post('/react/sendCode',{
            mobile:this.mobile.state.value
        }).then(res=>{

        })

        this.compoutedTime()
    }

    // 验证码发送后倒计时
    compoutedTime = () => {
        this.setState({
            count: --this.state.count,
            txt: `倒计时${this.state.count} S`,
            mobileFlag: false
        })

        var timer = setInterval(() => {
            if (this.state.count > 0) {
                this.setState({
                    count: --this.state.count,
                    txt: `倒计时${this.state.count} S`,
                    mobileFlag: false,
                    toggle: false
                })
            }else{
                clearInterval(timer);
                this.setState({
                    count: 60,
                    txt: '发送验证码',
                    mobileFlag: true,
                    toggle: true
                })
            }
        },1000)
    }


    render() {
        const {
            txt,
            mobileFlag,
            loginFlag
        } = this.state;
        return (
            <div className="login-box">
                <Head title='登录' show={true}></Head>
                <div className="login-centent">
                    <WhiteSpace />
                    <WingBlank>
                        <List>
                            <WhiteSpace />
                            <InputItem
                                type="tel"
                                placeholder="请输入手机号"
                                clear
                                ref={el => this.mobile = el}
                                onChange={this.checkMobile}
                            >手机号</InputItem>
                            <WhiteSpace />
                            <InputItem
                                type="number"
                                placeholder="请输入验证码"
                                // clear
                                ref={el => this.code = el}
                                onChange={this.checkCode}
                            >验证码</InputItem>
                            <Button
                                style={{height:42}}
                                onClick={this.sendCode}
                                disabled={!mobileFlag} type="warning" className="l-btn" size="large" inline >
                                {txt}
                            </Button>
                            <WhiteSpace />
                        </List>
                        <WhiteSpace />
                        <Button type="primary" onClick={this.todoLogin} disabled={!loginFlag} >登录</Button>
                    </WingBlank>
                </div>
            </div>
        )
    }
}