import React, {Component} from "react"
import './index.scss';
import {Switch,Route,Redirect} from "react-router-dom";
import  Home  from '../home';
import Shequ from '../shequ'
import { Swop } from "../swop";
import { My } from "../my";
import {Foot} from "&/components/foot";
import {Head} from "&/components/head";
import LazyLoad from '~/lazyload';


export class Main extends Component {
    render() {
        return (
            <div>
                <Switch>
                    <Route path='/main' exact render={()=>(<Redirect to="/main/home"/>)}/>
                    <Route path='/main/home' component={Home} />
                    <Route path='/main/shequ' component={Shequ}/>
                    <Route path='/main/swop' component={Swop}/>
                    <Route path='/main/my/' component={My}/>
                    <Route render={()=>(<Redirect to="/main/home"/>)}/>
                </Switch>
                <Foot></Foot>
                {/* <Head></Head> */}
            </div>
        )
    }
}