import React,{Component} from 'react';
import './index.scss';
import { NavLink } from "react-router-dom";


export const foots=[
    {path:"/main/home",name:"home",icon:"icon-home"},
    {path:"/main/shequ",name:"shequ",icon:"icon-svgclassifyselect"},
    {path:"/main/swop",name:"swop",icon:"icon-loading"},
    {path:"/main/my",name:"my",icon:"icon-minefill"}
]

export class Foot extends Component{
    state ={
        foots
    };
    render(){
        return(
            <footer>
                {
                    this.state.foots.map((foot,i)=>{
                        return(
                            <div key={i}>
                            <NavLink to={foot.path} activeClassName="nav-active">
                                <i className={'iconfont '+foot.icon}></i>
                            </NavLink>
                            </div>
                        )
                    })
                }
            </footer>
        )
    }
}