import React, { Component } from 'react';
import './index.scss';
import { Head } from '&/components/head';
import List from '&/components/list';
import { axios } from '~'
import { NavLink } from "react-router-dom";

class Ilike extends Component {
    state = {
        ilikes: []
    }

    componentDidMount() {
        const likes = []
        axios.post('/react/getWordInfo').then(res => {
            // console.log(res.data.result)
            for (let i = 0; i < res.data.result.length; i++) {
                likes.push(JSON.parse(res.data.result[i].word))
            }
            this.setState({
                ilikes: likes
            })
        })
    }

    render() {
        return (
            <div>
                <Head
                    title="我喜欢的"
                ></Head>
                <List
                    conentList={this.state.ilikes}
                >
                </List>
            </div>
        )
    }
}

export default Ilike